import React from 'react';
import { connect } from 'react-redux';
import { setAuthedUser } from '../actions/authedUser';
import Select from 'react-select';

export class Login extends React.Component {
	state = {
		authedUser: "",
		selectedValuePlaceholder: "select"
	}
	
	handleChange = ({value, label}) => 
	    this.setState({authedUser: value, selectedValuePlaceholder :label})
	
	handleLogin = (e) => {
        e.preventDefault()
        const { authedUser } = this.state
        const { setAuthedUser } = this.props
            if (authedUser) {
            setAuthedUser(authedUser)
            } 
			else 
				alert('Select a user!')
    }

	render() {
    const { users } = this.props
	const options = users.map(user => {
		const myOptions = [];
        myOptions.value = user.id;
	    myOptions.label = <div><img src = {user.avatarURL} height="20" alt={user.name}/>{user.name}</div>;
	    return myOptions;
    });
	
    return (
	    <div className="login">
	        <h1>Welcome to the Would You Rather App!</h1>
	        <h2>Please sign in to continue</h2>   
	            <form onSubmit={this.handleLogin}> 
	            <Select
	            value={options.value}
                placeholder={this.state.selectedValuePlaceholder}
                onChange={this.handleChange}
				options={options}
		        />  
		        <button className='button'>SIGN IN</button>
		        </form>            
	    </div>
	)
  }
}

function mapStateToProps({ users }) {
  return {
    users: Object.values(users)
  };
}

function mapDispatchToProps(dispatch) {
    return {
        setAuthedUser: (id) => {
            dispatch(setAuthedUser(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Login)